                     loadThrottle: 600,
                     // render: {
                     //     option: function(item, escape) {
                     //     	__pr(item);
                     //         return $compile(__Utils.template('#lwSelectizeOp', {
                     //             item : item
                     //         }))(scope);
                     //     }
                     // },
